import { useState, useEffect } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import './Banner.css'

// Assets
import { ArrowRightCircle } from 'react-bootstrap-icons'
import HeaderImg from '../../res/img/Vectary texture.png'

const Banner = () => {
    const [loopNum, setLoopNum] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);
    const toRotate = ["Game", "Graphics", "Generalist"]
    const [text, setText] = useState('');
    const [delta, setDelta] = useState(300 - Math.random() * 100);
    const period = 2000;

    useEffect(() => {
      let ticker = setInterval(() => {
        tick() 
      }, delta)
    
      return () => {
        clearInterval(ticker)
      }
    }, [text])
    
    const tick = () => {
        let i = loopNum % toRotate.length;
        let fullText = toRotate[i];
        let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

        setText(updatedText);
        if (isDeleting) {
            setDelta(prevDelta => prevDelta/2);
        }

        if (!isDeleting && updatedText === fullText) {
            setIsDeleting(true)
            setDelta(period);
        } else if (isDeleting && updatedText === '') {
            setIsDeleting(false);
            setLoopNum(loopNum + 1);
            setDelta(100);
        }
    }

    return (
        <section className="banner" id="home">
            <Container>
                <Row className="align-items-center">
                    <Col xs={12} md={6} xl={7}>
                        <span className="tagline">Welcome Viewers</span>
                        <h1>
                           <Row>
                                <Col xs={12} md={12} lg={4}>{`Hi I'm a `}</Col>
                                <Col className='roles-wrap' xs={12} lg={6}>
                                    <span data-text={text} className="wrap col-xl-7 col xs-12 col-md-6">{text}</span>
                                    {/* <span className="wrap col-xl-7 col xs-12 col-md-6">{text}</span>
                                    <span className="wrap col-xl-7 col xs-12 col-md-6">{text}</span> */}
                                </Col>
                           </Row>
                            {`Programmer`}
                            <p>I'm a game programmer who combines technical expertise with artistic vision to create captivating interactive experiences.</p>
                            <Row className='contact-container'>
                                <Col md={6}>
                                    <button onClick={(e) => {
                                        let contact = document.getElementById("connect");
                                        e.preventDefault();
                                        contact && contact.scrollIntoView();
                                    }}>Contact Me <ArrowRightCircle size={25}/></button>
                                </Col>
                                <Col md={6} className='cv-container'>
                                    <button className='download-cv' onClick={(e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => 
                                        window.open(
                                            "https://bit.ly/CV_Reyhanullah",
                                            '_blank'
                                        )
                                    }>
                                        <span>
                                            Download CV
                                        </span>
                                    </button>
                                    {/* <a href="https://bit.ly/CV_Reyhanullah" target="_blank" rel="noopener noreferrer">See my CV</a> */}
                                </Col>
                            </Row>
                        </h1>
                    </Col>
                    <Col xs={12} md={6} xl={5} className='phone3D'>
                        <img src={HeaderImg} alt="Header Img"/>
                    </Col>
                </Row>
            </Container> 
        </section>
    )
}

export default Banner