import React, { useEffect } from 'react'
import { useState } from 'react'
import { Container, Nav, Navbar, NavDropdown } from 'react-bootstrap'
import './NavBar.css'
import { log } from 'console'
import CONTACT_STRING from '../../res/string/contact.ts'

// Asset Import
import Logo from '../../res/img/empty-set-slash-svgrepo-com.png'
import GithubIcon from '../../res/img/icon/github-142-svgrepo-com.svg'
import DiscordIcon from '../../res/img/icon/discord-svgrepo-com.svg'
import EmailIcon from '../../res/img/icon/email-1572-svgrepo-com.svg'
import LinkedInLogo from '../../res/icon/linkedin.svg'

const NavBar = () => {
    const [activeLink, setActiveLink] = useState('home');
    const [scrolled, setScrolled] = useState(false);

    useEffect(() => {
        const onScroll = () => {
            if (window.scrollY > 50) {
                setScrolled(true);
            } else {
                setScrolled(false);
            }
        }

        window.addEventListener("scroll", onScroll);
        return () => window.removeEventListener("scroll", onScroll);
    }, [])

    const onUpdateActiveLink = (val:string) => {
        setActiveLink(val);
    }

    return (
        <Navbar expand="lg" className={scrolled ? "scrolled" : ""}>
        <Container>
            <Navbar.Brand href="#home">
                <img src={Logo} className='logo' alt="Logo" />
            </Navbar.Brand>
            <Navbar.Toggle aria-controls="basic-navbar-nav" >
                <span className="navbar-toggler-icon"></span>
            </Navbar.Toggle>
            <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
                <Nav.Link href="#home" className={activeLink === 'home' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('home')}>Home</Nav.Link>
                <Nav.Link href="#skills" className={activeLink === 'skills' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('skills')}>Skills</Nav.Link>
                <Nav.Link href="#projects" className={activeLink === 'projects' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('projects')}>Projects</Nav.Link>
            </Nav>
            <span className="navbar-text justify-content-around pt-2">
                <div className="social-icon">
                    <a target='_blank' rel='noreferrer' href={CONTACT_STRING.GITHUB_LINK}><img src={GithubIcon} width={25} alt="" /></a>
                    <a target='_blank' rel='noreferrer' href={CONTACT_STRING.DISCORD_LINK}><img src={DiscordIcon} width={25} alt="" /></a>
                    <a target='_blank' rel='noreferrer' href={CONTACT_STRING.LINKEDIN_LINK}><img src={LinkedInLogo} width={25} alt="" /></a>
                </div>
                <button className="vvd" onClick={(e) => {
                    let contact = document.getElementById("connect");
                    e.preventDefault();
                    contact && contact.scrollIntoView();
                }}>
                    <span>Contact Me</span>
                </button>
            </span>
            </Navbar.Collapse>
        </Container>
        </Navbar>
    )
}

export default NavBar