import React from 'react'
import './Project.css'
import ProjectTab from './ProjectTab'
import ProjectCard from './ProjectCard'
import { Container, Row, Col, Nav, Tab } from 'react-bootstrap'

// Import Assets
import projImg from '../../res/img/webgl-background.png'
import unityImg from '../../res/img/Quest4.png'
import threejsImg from '../../res/img/threejs.png'

const Project = () => {
  const projects_list = [
    {
      title: "WebGL Articulated Object & Animation",
      description: "Implementation of Articulated Model and it's animation in WebGL",
      imgUrl: projImg,
      link: 'https://github.com/reynull20/IF3260_Tugas3_K01_G15'
    },
    {
      title: "Extended Survival Shooter",
      description: "Survival Shooting with FPS Genre using Unity",
      imgUrl: unityImg,
      link: 'https://gitlab.informatika.org/reynull/if3210-2023-unity-tbs'
    },
    {
      title: "Three.js",
      description: "Sandbox of Three.js",
      imgUrl: threejsImg,
      link: 'https://github.com/reynull20/practice-webgl-threejs'
    }
  ]
  
  // Ref : https://react-bootstrap.github.io/components/navs/
  return (
    // <section className='project main-wrapper'>
    <section className='project' id='projects'>
      <Container>
        <Row>
          <Col>
            <h2>Projects</h2>
            <p>Checkout some of my recent Project that i have contributed with</p>
            <p>Also check my <a className='github-link' href='https://github.com/reynull20'>Github</a> for more</p>
            <Row>
              {
                projects_list.map((project, index) => {
                  return (
                    <ProjectCard
                      key={index}
                      title={project.title}
                      description={project.description}
                      imgUrl={project.imgUrl}
                      link={project.link}>
                      
                    </ProjectCard>
                  )
                })
              }
            </Row>
          </Col>
        </Row>
      </Container>
      {/* <div className='project content-wrapper'>
        <h1>Projects</h1>
        <div>
        <span>Here are some of the project that i have contributed so far. Check out my </span>
        <a href='https://github.com/reynull20?tab=repositories'>Github</a><span> or Email me to know more</span>
        </div>
        <ProjectTab />
      </div> */}
    </section>
    // <div className='project-container'>
    //     <section className='project tab container'>
    //         <h1 className='project-tab-title'>
    //           Project List
    //         </h1>
    //         <span>
    //           Checkout my recent Project that i have contributed with
    //         </span>
    //         <ProjectTab></ProjectTab>
    //     </section>
    // </div>
  )
}

export default Project