import React from 'react'
import { Container, Row, Col, Nav, Tab, TabContent, TabPane } from 'react-bootstrap'
import './Resume.css'

const Resume = () => {
  return (
    <section className='resume' id='resume'>
        <Container className='resume-container-wrapper'>
            <Row>
                <Col className='resume-container'>
                    <h2>Resume</h2>
                    <p>
                        "Here lies my short resume"
                    </p>
                    <Row className='resume-tab-wrap'>
                        <Tab.Container defaultActiveKey={"education"}>
                            <Col className='tab-col' md={6} xs={12}>
                                <Nav justify={true} className='flex-column' variant='tabs' defaultActiveKey={"education"}>
                                    <Nav.Item>
                                        <Nav.Link eventKey={"education"}>Education</Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey={"skill"}>Skill</Nav.Link>
                                    </Nav.Item>
                                    <Nav.Item>
                                        <Nav.Link eventKey={"interest"}>interest</Nav.Link>
                                    </Nav.Item>
                                </Nav>
                            </Col>
                            <Col md={6} xs={12}>
                                <Tab.Content className='tab-content-wrap'>
                                    <TabPane className='tab-panel-wrap' eventKey={"education"}>
                                        <ul>
                                            <li>
                                                <Row className='resume-item'>
                                                    <Col className='resume-item-title'>
                                                        <h6>Bandung Institute of Technology</h6>
                                                    </Col>
                                                    <Col className='time-duration'>
                                                        <h6>2019-present</h6>
                                                    </Col>
                                                </Row>
                                                <h6 className='resume-item-desc'>Bachelor of Informatics Engineering</h6>
                                            </li>
                                            <li>
                                                <Row className='resume-item'>
                                                    <Col className='resume-item-title'>
                                                        <h6>High School</h6>
                                                    </Col>
                                                    <Col className='time-duration'>
                                                        <h6>2015-2018</h6>
                                                    </Col>
                                                </Row>
                                                <h6 className='resume-item-desc'>SMAN 3 Bogor</h6>
                                            </li>
                                        </ul>
                                    </TabPane>
                                    <TabPane eventKey={"skill"}>
                                        <ul>
                                            <li>
                                                <Row className='resume-item'>
                                                    <Col className='resume-item-title'>
                                                        <h6>Unity</h6>
                                                    </Col>
                                                </Row>
                                                <div id='unity' className='resume-skill-bar'></div>
                                            </li>
                                            <li>
                                                <Row className='resume-item'>
                                                    <Col className='resume-item-title'>
                                                        <h6>C#</h6>
                                                    </Col>
                                                </Row>
                                                <div id='csharp' className='resume-skill-bar'></div>
                                            </li>
                                            <li>
                                                <Row className='resume-item'>
                                                    <Col className='resume-item-title'>
                                                        <h6>C++</h6>
                                                    </Col>
                                                </Row>
                                                <div id='cpp' className='resume-skill-bar'></div>
                                            </li>
                                            <li>
                                                <Row className='resume-item'>
                                                    <Col className='resume-item-title'>
                                                        <h6>C</h6>
                                                    </Col>
                                                </Row>
                                                <div id='c' className='resume-skill-bar'></div>
                                            </li>
                                            <li>
                                                <Row className='resume-item'>
                                                    <Col className='resume-item-title'>
                                                        <h6>Java</h6>
                                                    </Col>
                                                </Row>
                                                <div id='java' className='resume-skill-bar'></div>
                                            </li>
                                        </ul>
                                    </TabPane>
                                    <TabPane eventKey={"interest"}>
                                        <ul>
                                            <li>
                                                <Row className='resume-item'>
                                                    <Col className='resume-item-title'>
                                                        <h6>Casual Gaming</h6>
                                                    </Col>
                                                </Row>
                                                <h6 className='resume-item-desc'>I like to playing a casual game and exploring game mechanics as my main source of enjoyment</h6>
                                            </li>
                                            <li>
                                                <Row className='resume-item'>
                                                    <Col className='resume-item-title'>
                                                        <h6>Music</h6>
                                                    </Col>
                                                </Row>
                                                <h6 className='resume-item-desc'>Listening to music as a main source of relief in free time</h6>
                                            </li>
                                            <li>
                                                <Row className='resume-item'>
                                                    <Col className='resume-item-title'>
                                                        <h6>Coding</h6>
                                                    </Col>
                                                </Row>
                                                <h6 className='resume-item-desc'>Code things from scratch is my way of learning things as it helps me understand things better</h6>
                                            </li>
                                        </ul>
                                    </TabPane>
                                </Tab.Content>
                            </Col>
                        </Tab.Container>
                    </Row>
                </Col>
            </Row>
        </Container>
    </section>
  )
}

export default Resume